let amapFile = require('../../common/amap-wx.js');
let myAmapFun = new amapFile.AMapWX({
	key: '2649cf46ba2417b42f9fe36f53c57f63'
});
export default {
	data() {

		return {
			keywords: '',
			setUserData: {},
			show: true,
			markers: [],
			latitude: 23.13,
			longitude: 113.27,
			city: '',
			myAmapFunflag: '',
			dataArr: [],
			maskerClick: false,
			labels: []
		}
	},
	onLoad() {
		uni.getLocation({
			type: 'gcj02',
			success: (res) => {
				console.log(res)
				console.log('当前位置的经度：' + res.longitude);
				console.log('当前位置的纬度：' + res.latitude);
				let markers = []
				myAmapFun.getRegeo({
					iconPath: '../../static/img/marker.png',
					location: res.longitude + ',' + res.latitude, //经纬度
					success: (data) => {
						console.log(data)
						// let markersData = data.markers;

						this.markers = [{
							id: data[0].id,
							latitude: data[0].latitude,
							longitude: data[0].longitude,
							iconPath: data[0].iconPath,
							width: 40,
							height: 60,
							label: {
								content: data[0].name,
								fontSize: 16,
								bgColor: '#fff',
								textAlign: 'left',
								borderRadius: 5,
								padding: 3,
								color: 'red',
								width: 200
							}
						}]

						this.latitude = data[0].latitude

						this.longitude = data[0].longitude
						// that.showMarkerInfo(markersData, 0);
					},
					fail: function(info) {
						uni.showModal({
							title: info.errMsg
						})
					}
				})


			}
		});

	},
	methods: {
		makertap(e) {
			let id = e.markerId;
			let that = this;
			// that.showMarkerInfo(this.markers,id);
			that.changeMarkerColor(this.markers, id);
		},
		changeMarkerColor(data, i) {
			if (!this.maskerClick) {
				console.log(data)

				uni.showModal({
					content: "确定更改地址?",
					success: (res) => {
						if (res.confirm) {
							let that = this;
							let markers = [];
							let labels = []

							data[0].iconPath = "../../static/img/marker_checked.png";
							this.setUserMessage(data[0].label.content)
							this.maskerClick = true

							console.log(data)
							// data[j].iconPath = "../../static/img/marker_checked.png";
							markers.push({
								id: data[0].id,
								latitude: data[0].latitude,
								longitude: data[0].longitude,
								iconPath: data[0].iconPath,
								width: data[0].width,
								height: data[0].height,
								label: {
									content: data[0].label.content || data[0].regeocodeData.formatted_address,
									fontSize: 16,
									bgColor: '#fff',
									textAlign: 'right',
									borderRadius: 5,
									padding: 3,
									color: 'red'
								}
							})


							this.latitude = data[0].latitude

							this.longitude = data[0].longitude
							this.markers = markers
							console.log("可以了")
						}
					}
				})
			}
		},
		selectAdress(item) {
			this.maskerClick = false
			if (item.location.length === 0) {
				uni.showToast({
					title: '请输入具体地址',
					icon: 'none'
				})
				return false
			}
			this.longitude = item.location.split(',')[0]
			this.latitude = item.location.split(',')[1]
			this.show = false
			this.myAmapFunflag.getRegeo({
				iconPath: '../../static/img/marker.png',
				location: this.longitude + ',' + this.latitude, //经纬度
				success: (data) => {
					console.log(data)
					// let markersData = data.markers;
					this.maskerClick = true
					this.markers = [{
						id: data[0].id,
						latitude: data[0].latitude,
						longitude: data[0].longitude,
						iconPath: data[0].iconPath,
						width: 40,
						height: 60,
						label: {
							content: data[0].regeocodeData.formatted_address,
							fontSize: 16,
							bgColor: '#fff',
							textAlign: 'right',
							borderRadius: 5,
							padding: 3,
							color: 'red'
						}
					}]

					this.latitude = data[0].latitude

					this.longitude = data[0].longitude
					// that.showMarkerInfo(markersData, 0);


					uni.showModal({
						content: "确定更改地址?",
						success: (res) => {
							if (res.confirm) {
								let that = this;
								let markers = [];
								let labels = []
								data[0].iconPath = "../../static/img/marker_checked.png";
								markers.push({
									id: data[0].id,
									latitude: data[0].latitude,
									longitude: data[0].longitude,
									iconPath: data[0].iconPath,
									width: data[0].width,
									height: data[0].height,
									label: {
										content: data[0].regeocodeData.formatted_address,
										fontSize: 16,
										bgColor: '#fff',
										textAlign: 'right',
										borderRadius: 5,
										padding: 3,
										color: 'red'
									}
								})
								this.latitude = data[0].latitude
								this.longitude = data[0].longitude
								this.markers = markers
								this.setUserMessage(data[0].regeocodeData.formatted_address)
								console.log("可以了")
							}
						}
					})
				},
				fail: function(info) {
					uni.showModal({
						title: info.errMsg
					})
				}
			})
			console.log(this.myAmapFunflag)
		},

		//設置地址
		setUserMessage(address) {
			let tracePoint = ''
			this.setUserData = uni.getStorageSync('setUserData');
			console.log(this.setUserData)
			if (this.setUserData.role.id === 9 || this.setUserData.role.id === 10 || this.setUserData.role.id === 11) {
				if (this.setUserData.role.id === 9) {
					tracePoint = this.setUserData.merchant.merchantName
				} else {
					tracePoint = this.setUserData.distributeNode.tracePoint
				}
			} else {
				tracePoint = this.setUserData.merchant.merchantName
			}
			this.$common.post('/accountCenter/account/updateNode', {
				"location": address,
				"tracePoint": tracePoint
			}).then((res) => {

				if (res.data.statusCode === 200) {
					this.$common.showToast('修改成功', 'success')
					uni.setStorageSync('setUserData', res.data.data);
					setTimeout(() => {
						uni.navigateBack({
							url: -1
						})
					}, 2000)
				} else {
					this.$common.showToast(res.data.statusMsg, 'none')
				}
			})
		},
		bindInput(e) {
			if (!e.detail.value) {
				this.dataArr = []
				return false
			}

			this.myAmapFunflag = myAmapFun
			let that = this
			myAmapFun.getInputtips({
				keywords: e.detail.value,
				success: (data) => {
					console.log(data.tips)

					that.dataArr = data.tips
					console.log(that.dataArr)
				}
			})
			let params = {
				iconPathSelected: '../../static/img/marker_checked.png',
				iconPath: '../../static/img/marker.png',
				success: function(data) {
					console.log(data)
				},
			}
			myAmapFun.getPoiAround(params)

		}
	}
}
